<template>
    <div class="hello">
        <div class="user-header">
            <input type="file" name="image" accept="image/*" @change='onchangeImgFun'
                class="header-upload-btn user-header-com">
            <img alt="" :src='imgStr' class="user-header-img user-header-com">
            <p class="tip">50kb <span class="error">{{errorStr}}</span></p>
        </div>
    </div>
</template>
<!-- input accept -->
<script>
    export default {
        name: 'HelloWorld',
        data() {
            return {
                imgStr: require('../assets/img/picture.png'),
                errorStr: ''
            }
        },
        methods: {
            onchangeImgFun(e) {
                var file = e.target.files[0]
                console.log(file)
                // 
                let imgSize = file.size
                console.log(imgSize)
                var _this = this // this
                // 5Mb
                if (imgSize <= 50 * 1024) {
                    // 
                    _this.errorStr = ''
                    console.log('')
                    // 
                    //  1
                    // this.imgStr = window.URL.createObjectURL(e.target.files[0])
                    // console.log(window.URL.createObjectURL(e.target.files[0])) // 

                    // base64 2
                    var reader = new FileReader()
                    reader.readAsDataURL(file) //  base64
                    reader.onloadend = function () {
                        //  base64 ,  img  src 
                        var dataURL = reader.result
                        console.log(dataURL)
                        _this.imgStr = dataURL
                        // 
                    }
                } else {
                    console.log('')
                    _this.errorStr = ''
                }
            }
        }

    }
</script>


<style scoped>
    .user-header {
        position: relative;
        display: inline-block;
    }

    .user-header-com {
        width: 144px;
        height: 144px;
        display: inline-block;
    }

    .header-upload-btn {
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        /* inputimg */
    }

    .tip {
        font-size: 14px;
        color: #666;
    }

    /* error */
    .error {
        font-size: 12px;
        color: tomato;
        margin-left: 10px;
    }
</style>


