<template>
        <div class="account-password">
            <elm-header :fatherName="fatherName"></elm-header>
            <div class="account-password-main">
                <label for="oldPwd"><input type="password" placeholder="" v-model="oldPassword" @change="userInputOldPassword"/></label>
                <label class="account-password-newPWD" for="newPwd"><input type="password" placeholder="" v-model="newPassword" /></label>
                <p>8~32</p>
                <button class="account-password-button" @click="sendChanged"></button>
            </div>
        </div>
    </template>
    
    <script>
    import { mapState } from "vuex"
    export default{
        name:"accountPassword",
        data () {
            return {
                fatherName:"",
                newPassword:null,
                oldPassword:null,
            }
        },
        computed: mapState(['userInfor']),
        methods: {
            sendChanged:function(){
                this.$router.push("/account");
            },
            userInputOldPassword:function(){
                if(this.oldPassword!==this.userInfor.userPassword){
                    alert("")
                }else{
                    alert("")
                }
            }
        }

    }
    
    </script>

<style scoped>
.account-password-main{
    padding:2rem 0.8rem 0;
}
.account-password label{
    display: block;
    width:100%;
    border-bottom:1px solid #999;
    padding:0.3rem 0;
    font-size:0.3rem;
    color:black;
    font-weight: bold;
}
.account-password input{
    border:none;
    outline: none;
    background: transparent;
    font-weight: normal;
}
.account-password-newPWD{
    margin-top:0.5rem;
}
.account-password p{
    margin-top:0.2rem;
}
.account-password-button{
    width:100%;
    background: #10a6cc;
    outline:none;
    border:none;
    color:#fff;
    font-size: 0.36rem;
    padding:0.2rem 0;
    border-radius: 0.5rem;
    margin-top:1rem;

}
</style>


