<template>
    <div class="login">
        <elm-header :fatherName="fatherName"></elm-header>
        <form>
            <template v-if="parameter=='register'">
                <mt-field label="" :state="userCheck" placeholder="" v-model="username"></mt-field>
                <mt-field label="" :state="userCheck" placeholder="" type="password" v-model="password"></mt-field>
                <mt-field label="" :state="userCheck" placeholder="" type="tel" v-model="phone"></mt-field>
                <mt-field label="" :state="userCheck" v-model="captcha">
                    <img src="../../static/img/Verification-Code.png" height="45px" width="100px">
                </mt-field>
            </template>
            <template v-if="parameter=='signin_pwd'">
                    <mt-field label="" placeholder="" v-model="username"></mt-field>
                    <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
                    <mt-field label="" v-model="captcha">
                        <img src="../../static/img/Verification-Code.png" height="45px" width="100px">
                    </mt-field>
            </template>
            <template v-if="parameter=='signin_phone'">
                <mt-field label="" placeholder="" type="tel" v-model="phone"></mt-field>
                <mt-field label="" v-model="captcha">
                    <img src="../../static/img/Verification-Code.png" height="45px" width="100px">
                </mt-field>

            </template>
        </form>
        <div class="login-submit">
                <mt-button v-if="parameter=='register'" type="primary" size="large" ></mt-button>
                <mt-button v-else type="primary" size="large"></mt-button>
        </div>
    </div>
</template>

<script>

export default{
    name:'login',
    data () {
        return {
            login_phone:false,
            fatherName:null,
            parameter:null,
            username:null,
            password:null,
            captcha:null,
            phone:null,
        }
    },
    created() {
        let logoin_parameter=this.$route.params.login_parameter;
        this.parameter=logoin_parameter;
        if(logoin_parameter==="register"){
            this.fatherName="";
        }else{
            this.fatherName="";
        }
    },
    mounted() {

    },
    watch: {
        $route:function(to,from){
            this.parameter = to.params.login_parameter;
            //console.log(this.parameter)
            if (this.parameter === "register") {
                this.fatherName = "";
            }else{
                this.fatherName = "";
            }
            //console.log(this.fatherName)
        }
    },
    methods: {

    },
    watch: {
        username:function(newval,oldval){
            if(newval.length>20){
                alert("20");
                this.userCheck="error";
            }else{
                this.userCheck="success";
            }
        }
    }
}

</script>

<style scoped>
.login form{
    margin-top:0.2rem;
    background:#fff;
    padding:0 0.3rem 
}
.login-submit{
    margin-top:1.4rem;
}

</style>


